/*global kakao*/ 
import React, { useEffect }  from 'react';
import { firestore } from "./firebase";
import styled from 'styled-components';


const Location=({homeRef})=>{

    useEffect(()=>{
      

    
      var container = document.getElementById('map');
      var options = {
        center: new kakao.maps.LatLng(37.5412425502694 , 126.96830615752471),
        level: 3
      };
      var map = new kakao.maps.Map(container, options);

      const ping = firestore.collection("ping");

      ping.get().then((docs) => {
        docs.forEach((doc) => {
          if(doc.exists){
            const { Lat, Lng, ppm } = doc.data();

            const markerPosition = new kakao.maps.LatLng(Lat, Lng);
          const marker = new kakao.maps.Marker({
            position: markerPosition,
            map: map
          });

          const infowindowContent = `<div style="color: #000;">${ppm} ppm</div>`;
          const infowindow = new kakao.maps.InfoWindow({
            content: infowindowContent
          });

          kakao.maps.event.addListener(marker, 'click', function () {
            infowindow.open(map, marker);
          });

            console.log(doc.data());
            console.log(doc.id);
          }
        });
      })

      }, [])
  
  
      return (
          <MapContainer ref={homeRef}>
              <div id="map" style={{width:"100%", height:"1080px"}}></div> 
          </MapContainer>
      )
  }

const MapContainer = styled.div`
  height: 1080px; /* Set to 100% so it takes up the entire height of its parent */
`;
  
  export default Location;