import React from 'react';
import styled from "styled-components";
import { Link, animateScroll as scroll } from 'react-scroll';
import "./nav.css";

const Nav = ({sc1, sc2, sc3}) => {
  return (
    <Container>
      <Containertwo>
        <Navtitle onClick={sc1}>파리효과</Navtitle>
        <NextContainer>
          <NavItem onClick={sc2}>도움말</NavItem>
          <NavItem onClick={sc3}>소개</NavItem>
        </NextContainer>
      </Containertwo>
    </Container>
  );
};

const Container = styled.div`
  display: flex;
  width: 100%;
  height: 60px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  background: #000;
`;

const Navtitle = styled.div`
  font-family: "pretendardblack";
  color: #00DC23;
  cursor: pointer;
  
`;

const NavItem = styled.div`
  font-family: "pretendardsb";
  color: white;
  cursor: pointer;
`;

const Containertwo = styled.div`
  display: flex;
  width: 100%;
  max-width: 1537px; /* Set a maximum width */
  height: 100%;
  padding: 0px 20px; /* Adjust padding as needed */
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
  background: #000;
  margin: 0 auto; /* Center the container */
`;

const NextContainer = styled.div`
  gap: 100px; /* Adjust the gap as needed */
  display: flex;
  align-items: center;
`;



export default Nav;