import React from 'react';
import styled from 'styled-components';
import Bg from './img/im_back.png'
import "./nav.css";

const Introduce = ({introduceRef}) => {
  return (
    <div>
      <Imageback ref={introduceRef}>
        <Title>
            도움말
        </Title>
        <SubTitle>
         파리 기후 협약은 무엇인가요?
        </SubTitle>
        <MainText>
        2015년 유엔 기후 변화 회의에서 채택된 조약입니다. <br/>
        지구 평균온도 상승 폭을 산업화 이전 대비 2℃ 이하로 유지하고, <br/>
        더 나아가 온도 상승 폭을 1.5℃ 이하로 제한하기 위해 <br/>
        함께 노력하기 위한 국제적인 협약입니다.<br/>
        </MainText>
      </Imageback>
    </div>
  );
};


const Title = styled.div`
  color: #00DC23;
  font-family: "pretendardb";
  font-size: 64px;
`;

const SubTitle = styled.div`
    margin-top: 8px;
  color: white;
  font-family: "pretendardsb";
  font-size: 36px;
`;

const MainText = styled.div`
white-space: pre-wrap;
margin-top: 46px;
text-align: center;
  color: white;
  font-family: "pretendarlg";
  font-size: 32px;
`;

const Imageback = styled.div`
  background-image: url(${Bg});
  background-size: cover; /* Use 'cover' to fill the entire container */
  height: 100vh; /* Set the height to 100vh to make it fill the viewport height */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white; /* Example text color */
`;

export default Introduce;
