import React from 'react';
import styled from 'styled-components';
import Bg from './img/im_back.png'
import "./nav.css";

const Introduce = ({introducetwoRef}) => {
  return (
    <div>
      <Imageback ref={introducetwoRef}>
        <Title>
            소개
        </Title>
            <MainText>
            "파리효과" 서비스는 공장의 이산화 탄소 농도를 측정하고 체계적으로 정리하는 플랫폼으로, <br/>
            파리 기후 협약의 이행을 촉진하는 데 기여하고 있습니다. <br/>
            저희는 이 서비스를 통해 정부 및 기업이 파리 기후 협약의 목표를 달성하기 위한 효과적인 정책을 수립하는 데 도움을 제공하고 있습니다.<br/>

            또한, 사람들에게 파리 기후 협약에 대한 인식을 높이기 위한 노력으로서 이 서비스를 활용하고 있습니다. <br/>
            이를 통해 우리는 지구 온난화 및 기후 변화와 같은 긴급한 문제에 대한 대처를 촉진하고, <br/>
            지속 가능한 에너지 및 환경 관리에 대한 중요성을 강조하고자 합니다.<br/>
            "파리효과"는 측정된 데이터를 직관적이고 알기 쉬운 형태로 제공하여, <br/>
            관계자들이 보다 효과적으로 높은 이산화 탄소 배출을 관리하고 개선할 수 있도록 돕고 있습니다. <br/>
            함께해, 우리의 노력이 더 깨끗하고 지속 가능한 미래를 향한 첫걸음이 되도록 합시다.<br/>
            </MainText>
            <MainText>
            We are a platform dedicated to measuring and systematically organizing carbon dioxide levels in factories. <br/>
            Our mission is to contribute to the fulfillment of the Paris Climate Agreement by facilitating the creation of policies that effectively address its goals. <br/>
            Through our service, we aim to assist governments and businesses in formulating strategies that align with the objectives of the Paris Climate Agreement.<br/>

            Additionally, we strive to raise awareness about the Paris Climate Agreement among individuals.<br/>
            By utilizing our service, we seek to enhance public understanding of urgent issues such as global warming and climate change.<br/>
            We emphasize the significance of sustainable energy and environmental management,<br/>
            fostering a collective commitment to address these pressing challenges.<br/>
            Our website presents measured data in an intuitive and easily comprehensible format,<br/>
            empowering stakeholders to manage and improve carbon emissions more efficiently.<br/>
            Join us in our efforts as we take steps towards a cleaner and more sustainable future. Together, let's make our contribution to a greener world.<br/>
            </MainText>
      </Imageback>
    </div>
  );
};


const Title = styled.div`
  color: #00DC23;
  font-family: "pretendardb";
  font-size: 64px;
`;


const MainText = styled.div`
    white-space: pre-wrap;
    margin-top: 42px;
    text-align: center;
    color: white;
    font-family: "pretendarlg";
    font-size: 20px;
    font-weight: 400;
    line-height: normal;
`;

const Imageback = styled.div`
  background-size: cover; /* Use 'cover' to fill the entire container */
  height: 100vh; /* Set the height to 100vh to make it fill the viewport height */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #000; /* Example text color */
`;

export default Introduce;
