import React from 'react';
import Location from './contents/main'
import Nav from './contents/nav'
import Introduce from './contents/introduce'
import Introducetwo from './contents/introducetwo'
import styled from "styled-components";
import 'aos/dist/aos.css'
import AOS from "aos";
import './App.css';
import { useEffect, useRef } from 'react';

function App() {
  const home = useRef(null)
  const introduce = useRef(null)
  const introducetwo = useRef(null)

  const scrollhome = () => {
    if (home != null) {
      const { offsetTop } = home.current;
      window.scrollTo({behavior: "smooth", top: offsetTop});
    }
  }
  const scrollintroduce = () => {
    if (introduce != null) {
      const { offsetTop } = introduce.current;
      window.scrollTo({behavior: "smooth", top: offsetTop});
    }
  }
  const scrollintroducetwo = () => {
    if (introducetwo != null) {
      const { offsetTop } = introducetwo.current;
      window.scrollTo({behavior: "smooth", top: offsetTop});
    }
  }

  useEffect(() => {
    AOS.init();
  }, [])

  
  return (
    <>
    <Nav c1={scrollhome} sc2={scrollintroduce} sc3 = {scrollintroducetwo}/>
    <Location homeRef={home}/>
    <Introduce introduceRef={introduce}/>
    <Introducetwo introducetwoRef={introducetwo}/>
    </>

  );
}


const MainPage = styled.div`
  height: 100vh; /* Use 100vh to make it fill the entire viewport height */
  display: flex;
  flex-direction: column; /* Ensure child components are stacked vertically */
`;


export default App;
